.breed-select {
  display: block;
}

.breed-select-hidden {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  border: 3px solid #000000;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid;
  border-color: #ec3bba transparent;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loader-hiden {
  display: none;
}

.cat-info {
  display: flex;
  gap: 20px;
}

.cat-info-hiden {
  display: none;
}
.ss-arrow {
  fill: #000000;
  width: 16px;
  height: 16px;
  margin-right: 5px;
}
