.breed-select {
  display: block;
}

.breed-select-hidden {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid #ec3bba;
  border-color: #ec3bba rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader-hiden {
  display: none;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.cat-info-hiden {
  display: none;
}

.ss-arrow {
  fill: #000;
  width: 16px;
  height: 16px;
  margin-right: 5px;
}

